@import "@/assets/style/mixins.scss"; @import "@/assets/style/variables.scss";





























.btb-about-package{
  .package_title {
    @include font-md-b;
    display: inline-block;

    @include media-breakpoint-down-md {
      @include font-lg-b;
    }
  }

  .package_description {
    @include media-breakpoint-down-md {
      margin-top: 0.5rem;
    }
  }
  .package_link {
    margin-left: 0.5rem;
    color: $color-gray;
    cursor: pointer;

    &:hover{
      color: $color-primary;
    }
  }
  .package_version,
  .package_updated {
    @include margin-sm-y;
    color: $color-gray-l;
  }
}
